<template>
  <div style="position:absolute" :style="styled">
    <v-img :src="require('@/assets/bubble.svg')" />
  </div>
</template>


<script>
export default {
    props : {
        x : String,
        y : String,
        degree : String
    },
    computed : {
        styled(){
            return "left:" + this.x + "; top:" + this.y + "; transform : rotate(" + this.degree + "deg);"; 
        }
    }
};
</script>
